/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
  .post-wrapper {
    width: 100%;
  }
  .post {
    width: 98%;
    max-width: 100%;
    margin: 2%;
    flex-direction: row;

    .post-thumbnail {
      width: 30%;
    }

    .post-content {
      width: 100%;
      .post-title {
        margin: 0 0 5px;
        a {
          font-size: 1rem;
        }
      }
    }
  }
}

