  .sidebar {
    display: none;
  }
  .about {
    text-align: center;
    max-width: 480px;
    margin: 10px auto auto;
    img {
      width: 100px;
      height: 100px;
      border-radius: 100%;
      margin-bottom: 10px;
    }
}

  .content-box {
    padding: 20px 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
  }

.contact-title, .copyright {
  display: none;
}

}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
  .content-box {
    padding: 10px;
  }
  .post-wrapper {
    width: 100%;
  }
  .post {
    width: 98%;
    max-width: 100%;
    margin: 2%;
    flex-direction: row;

    .post-thumbnail {
      width: 30%;
    }

    .post-content {
      width: 100%;
      .post-title {
        margin: 0 0 5px;
        a {
          font-size: 1rem;
        }
      }
      .post-descriptions {
        display: none;
      }
    }
  }
}
